.main-container {
    border: 5px;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
}
nav {
    height: 8vh;
    background-color: black;
}

.songLabels {
    border: 5px;
}
.controls {
    border: 5px;
    margin: auto;
}
.controls > input {
    max-height: 100px;
    height: 50px;
    border-radius: 50%;
    margin: 10px;
    margin-left: 15px;
}
.card {
    width: 100%;
    min-width: 300px;
    height: 70vh;
}
.card .card-image {
    width: 100%;
    min-width: 300px;
}

#play {
    background-color: green;
    margin-right: 5px;
    height: 50px;
    margin: 10px;
}

#stop {
    background-color: firebrick;
    margin-right: 5px;
    height: 50px;
    margin: 10px;
}

.pulse {
    border-radius: 50%;
    background: #cca92c;
    cursor: pointer;
    -webkit-box-shadow: 0 0 0 rgba(204,169,44, 0.4);
            box-shadow: 0 0 0 rgba(204,169,44, 0.4);
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
  }
  .pulse:hover {
    -webkit-animation: none;
            animation: none;
  }

  @-webkit-keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(000,169,44, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 10px rgba(34, 115, 177, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(187, 17, 17, 0);
    }
  }
  @keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(0, 145, 94, 0.879);
              box-shadow: 0 0 0 0 rgba(0, 145, 94, 0.879);
    }
    70% {
        -webkit-box-shadow: 0 0 0 10px rgba(29, 0, 118, 0.892);
                box-shadow: 0 0 0 10px rgba(29, 0, 118, 0.892);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(10, 60, 186, 0.968);
                box-shadow: 0 0 0 0 rgba(10, 60, 186, 0.968);
    }
  }

.grow { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }
.grow:hover { -webkit-transform: scale(1.5); -ms-transform: scale(1.5); transform: scale(1.5); }

/* CSS for CANVAS */
.konvajs-content {
  margin:auto;
}

@media only screen and (max-width: 500px) {
    .main-container {
        display:-ms-flexbox;
        display:flex;
        -ms-flex-flow: column;
            flex-flow: column;

    }
}


